'use strict';
module.exports = function localize_ja(errors) {
  if (!(errors && errors.length)) return;
  for (var i = 0; i < errors.length; i++) {
    var e = errors[i];
    var out;
    switch (e.keyword) {
      case '$ref':
        out = '' + (e.params.ref) + '';
        break;
      case 'additionalItems':
        out = '';
        var n = e.params.limit;
        out += '' + (n) + '';
        break;
      case 'additionalProperties':
        out = '';
        break;
      case 'anyOf':
        out = '"anyOf"';
        break;
      case 'const':
        out = 'should be equal to constant';
        break;
      case 'contains':
        out = 'should contain a valid item';
        break;
      case 'custom':
        out = 'should pass "' + (e.keyword) + '" keyword validation';
        break;
      case 'dependencies':
        out = '' + (e.params.property) + '';
        var n = e.params.depsCount;
        out += '' + (e.params.deps) + '';
        break;
      case 'enum':
        out = '';
        break;
      case 'exclusiveMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += (cond) + '';
        break;
      case 'exclusiveMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += (cond) + '';
        break;
      case 'false schema':
        out = 'boolean schema is false';
        break;
      case 'format':
        out = '"' + (e.params.format) + '"';
        break;
      case 'formatExclusiveMaximum':
        out = 'formatExclusiveMaximum should be boolean';
        break;
      case 'formatExclusiveMinimum':
        out = 'formatExclusiveMinimum should be boolean';
        break;
      case 'formatMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'should be ' + (cond);
        break;
      case 'formatMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'should be ' + (cond);
        break;
      case 'if':
        out = 'should match "' + (e.params.failingKeyword) + '" schema';
        break;
      case 'maximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += (cond) + '';
        break;
      case 'maxItems':
        out = '';
        var n = e.params.limit;
        out += '' + (n) + '';
        break;
      case 'maxLength':
        out = '';
        var n = e.params.limit;
        out += '' + (n) + '';
        break;
      case 'maxProperties':
        out = '';
        var n = e.params.limit;
        out += '' + (n) + '';
        break;
      case 'minimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += (cond) + '';
        break;
      case 'minItems':
        out = '';
        var n = e.params.limit;
        out += '' + (n) + '';
        break;
      case 'minLength':
        out = '';
        var n = e.params.limit;
        out += '' + (n) + '';
        break;
      case 'minProperties':
        out = '';
        var n = e.params.limit;
        out += '' + (n) + '';
        break;
      case 'multipleOf':
        out = '' + (e.params.multipleOf) + '';
        break;
      case 'not':
        out = '"not"';
        break;
      case 'oneOf':
        out = '"oneOf"';
        break;
      case 'pattern':
        out = '"' + (e.params.pattern) + '"';
        break;
      case 'patternRequired':
        out = 'should have property matching pattern "' + (e.params.missingPattern) + '"';
        break;
      case 'propertyNames':
        out = 'property name \'' + (e.params.propertyName) + '\' is invalid';
        break;
      case 'required':
        out = '' + (e.params.missingProperty) + '';
        break;
      case 'switch':
        out = 'should pass "switch" keyword validation, case ' + (e.params.caseIndex) + ' fails';
        break;
      case 'type':
        out = '' + (e.params.type) + '';
        break;
      case 'uniqueItems':
        out = '' + (e.params.j) + '' + (e.params.i) + '';
        break;
      default:
        continue;
    }
    e.message = out;
  }
};
