'use strict';
module.exports = function localize_ru(errors) {
  if (!(errors && errors.length)) return;
  for (var i = 0; i < errors.length; i++) {
    var e = errors[i];
    var out;
    switch (e.keyword) {
      case '$ref':
        out = '   ' + (e.params.ref);
        break;
      case 'additionalItems':
        out = '';
        var n = e.params.limit;
        out += '   ,  ' + (n) + ' ';
        if (n >= 2 && n <= 4) {
          out += '';
        } else if (n != 1) {
          out += '';
        }
        break;
      case 'additionalProperties':
        out = '    ';
        break;
      case 'anyOf':
        out = '      "anyOf"';
        break;
      case 'const':
        out = '    ';
        break;
      case 'contains':
        out = '    ';
        break;
      case 'custom':
        out = '   "' + (e.keyword) + '"';
        break;
      case 'dependencies':
        out = '';
        var n = e.params.depsCount;
        out += '  ';
        if (n == 1) {
          out += '';
        } else {
          out += '';
        }
        out += ' ' + (e.params.deps) + ',    ' + (e.params.property);
        break;
      case 'enum':
        out = '       "enum"';
        break;
      case 'exclusiveMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += '  ' + (cond);
        break;
      case 'exclusiveMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += '  ' + (cond);
        break;
      case 'false schema':
        out = '  false';
        break;
      case 'format':
        out = '   "' + (e.params.format) + '"';
        break;
      case 'formatExclusiveMaximum':
        out = 'formatExclusiveMaximum   boolean';
        break;
      case 'formatExclusiveMinimum':
        out = 'formatExclusiveMinimum   boolean';
        break;
      case 'formatMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += '  ' + (cond);
        break;
      case 'formatMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += '  ' + (cond);
        break;
      case 'if':
        out = '  e "' + (e.params.failingKeyword) + '"';
        break;
      case 'maximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += '  ' + (cond);
        break;
      case 'maxItems':
        out = '';
        var n = e.params.limit;
        out += '   ,  ' + (n) + ' ';
        if (n >= 2 && n <= 4) {
          out += '';
        } else if (n != 1) {
          out += '';
        }
        break;
      case 'maxLength':
        out = '';
        var n = e.params.limit;
        out += '   ,  ' + (n) + ' ';
        if (n >= 2 && n <= 4) {
          out += '';
        } else if (n != 1) {
          out += '';
        }
        break;
      case 'maxProperties':
        out = '';
        var n = e.params.limit;
        out += '   ,  ' + (n) + ' ';
        if (n == 1) {
          out += '';
        } else if (n >= 2 && n <= 4) {
          out += '';
        } else {
          out += '';
        }
        break;
      case 'minimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += '  ' + (cond);
        break;
      case 'minItems':
        out = '';
        var n = e.params.limit;
        out += '   ,  ' + (n) + ' ';
        if (n >= 2 && n <= 4) {
          out += '';
        } else if (n != 1) {
          out += '';
        }
        break;
      case 'minLength':
        out = '';
        var n = e.params.limit;
        out += '   ,  ' + (n) + ' ';
        if (n >= 2 && n <= 4) {
          out += '';
        } else if (n != 1) {
          out += '';
        }
        break;
      case 'minProperties':
        out = '';
        var n = e.params.limit;
        out += '   ,  ' + (n) + ' ';
        if (n == 1) {
          out += '';
        } else if (n >= 2 && n <= 4) {
          out += '';
        } else {
          out += '';
        }
        break;
      case 'multipleOf':
        out = '   ' + (e.params.multipleOf);
        break;
      case 'not':
        out = '     "not"';
        break;
      case 'oneOf':
        out = '     e  "oneOf"';
        break;
      case 'pattern':
        out = '   "' + (e.params.pattern) + '"';
        break;
      case 'patternRequired':
        out = '  ,   "' + (e.params.missingPattern) + '"';
        break;
      case 'propertyNames':
        out = '  \'' + (e.params.propertyName) + '\'   ';
        break;
      case 'required':
        out = '    ' + (e.params.missingProperty);
        break;
      case 'switch':
        out = '   ' + (e.params.caseIndex) + '  "switch"';
        break;
      case 'type':
        out = '  ' + (e.params.type);
        break;
      case 'uniqueItems':
        out = '     ( ' + (e.params.j) + '  ' + (e.params.i) + ' )';
        break;
      default:
        continue;
    }
    e.message = out;
  }
};
