'use strict';
module.exports = function localize_sk(errors) {
  if (!(errors && errors.length)) return;
  for (var i = 0; i < errors.length; i++) {
    var e = errors[i];
    var out;
    switch (e.keyword) {
      case '$ref':
        out = 'referenciu ' + (e.params.ref) + ' sa nepodarilo njs';
        break;
      case 'additionalItems':
        out = '';
        var n = e.params.limit;
        out += 'neme obsahova viac, ne ' + (n) + ' prv';
        if (n == 1) {
          out += 'ok';
        } else {
          out += 'kov';
        }
        break;
      case 'additionalProperties':
        out = 'neme obsahova alie poloky';
        break;
      case 'anyOf':
        out = 'mus splova aspo jednu zo schm v "anyOf"';
        break;
      case 'const':
        out = 'mus by kontanta';
        break;
      case 'contains':
        out = 'mus obsahova prvok zodpovedajci schme';
        break;
      case 'custom':
        out = 'mus splni "' + (e.keyword) + '" validciu';
        break;
      case 'dependencies':
        out = '';
        var n = e.params.depsCount;
        out += ' mus obsahova polo';
        if (n >= 2 && n <= 4) {
          out += 'ky';
        } else if (n != 1) {
          out += 'iek';
        } else {
          out += 'ka';
        }
        out += ': ' + (e.params.deps) + ', ak obsahuje ' + (e.params.property);
        break;
      case 'enum':
        out = 'mus by jedna z definovanch hodnt';
        break;
      case 'exclusiveMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus by ' + (cond);
        break;
      case 'exclusiveMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus by ' + (cond);
        break;
      case 'false schema':
        out = 'schma je false';
        break;
      case 'format':
        out = 'mus obsahova formt "' + (e.params.format) + '"';
        break;
      case 'formatExclusiveMaximum':
        out = 'formatExclusiveMaximum mus by boolean';
        break;
      case 'formatExclusiveMinimum':
        out = 'formatExclusiveMinimum mus by boolean';
        break;
      case 'formatMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus by ' + (cond);
        break;
      case 'formatMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus by ' + (cond);
        break;
      case 'if':
        out = 'should match "' + (e.params.failingKeyword) + '" schema';
        break;
      case 'maximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus by ' + (cond);
        break;
      case 'maxItems':
        out = '';
        var n = e.params.limit;
        out += 'nesmie obsahova viac ne ' + (n) + ' prv';
        if (n == 1) {
          out += 'ok';
        } else {
          out += 'kov';
        }
        break;
      case 'maxLength':
        out = '';
        var n = e.params.limit;
        out += 'nesmie by dlh ne ' + (n) + ' znak';
        if (n != 1) {
          out += 'ov';
        }
        break;
      case 'maxProperties':
        out = '';
        var n = e.params.limit;
        out += 'nesmie obsahova viac ne ' + (n) + ' polo';
        if (n >= 2 && n <= 4) {
          out += 'ky';
        } else if (n != 1) {
          out += 'iek';
        } else {
          out += 'ka';
        }
        break;
      case 'minimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus by ' + (cond);
        break;
      case 'minItems':
        out = '';
        var n = e.params.limit;
        out += 'nesmie obsahova menej ne ' + (n) + ' prv';
        if (n == 1) {
          out += 'ok';
        } else {
          out += 'kov';
        }
        break;
      case 'minLength':
        out = '';
        var n = e.params.limit;
        out += 'nesmie by krat ne ' + (n) + ' znak';
        if (n != 1) {
          out += 'ov';
        }
        break;
      case 'minProperties':
        out = '';
        var n = e.params.limit;
        out += 'nesmie obsahova menej ne ' + (n) + ' polo';
        if (n >= 2 && n <= 4) {
          out += 'ky';
        } else if (n != 1) {
          out += 'iek';
        } else {
          out += 'ka';
        }
        break;
      case 'multipleOf':
        out = 'mus by nsobkom ' + (e.params.multipleOf);
        break;
      case 'not':
        out = 'nesmie splova schmu v "not"';
        break;
      case 'oneOf':
        out = 'mus splova prve jednu schmu v "oneOf"';
        break;
      case 'pattern':
        out = 'mus splova regulrny vraz "' + (e.params.pattern) + '"';
        break;
      case 'patternRequired':
        out = 'mus obsahova poloku spljcu regulrny vraz "' + (e.params.missingPattern) + '"';
        break;
      case 'propertyNames':
        out = 'nzov poloky \'' + (e.params.propertyName) + '\' nezodpoved schme';
        break;
      case 'required':
        out = 'mus obsahova poadovan poloku ' + (e.params.missingProperty);
        break;
      case 'switch':
        out = 'mus prejs validciou "switch", prpad ' + (e.params.caseIndex) + ' je nespen';
        break;
      case 'type':
        out = 'mus by ' + (e.params.type);
        break;
      case 'uniqueItems':
        out = 'nesmie obsahova duplicitn prvky (prvky ## ' + (e.params.j) + ' a ' + (e.params.i) + ' s rovnak)';
        break;
      default:
        continue;
    }
    e.message = out;
  }
};
