'use strict';
module.exports = function localize_th(errors) {
  if (!(errors && errors.length)) return;
  for (var i = 0; i < errors.length; i++) {
    var e = errors[i];
    var out;
    switch (e.keyword) {
      case '$ref':
        out = ' reference ' + (e.params.ref);
        break;
      case 'additionalItems':
        out = '';
        var n = e.params.limit;
        out += ' ' + (n);
        break;
      case 'additionalProperties':
        out = ' property ';
        break;
      case 'anyOf':
        out = ' schema  "anyOf"';
        break;
      case 'const':
        out = '';
        break;
      case 'contains':
        out = '';
        break;
      case 'custom':
        out = ' "' + (e.keyword) + '"';
        break;
      case 'dependencies':
        out = '';
        var n = e.params.depsCount;
        out += ' property ' + (e.params.property) + '  property ' + (e.params.deps) + ' ';
        break;
      case 'enum':
        out = '';
        break;
      case 'exclusiveMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += ' ' + (cond);
        break;
      case 'exclusiveMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += ' ' + (cond);
        break;
      case 'false schema':
        out = 'schema  false';
        break;
      case 'format':
        out = ' "' + (e.params.format) + '"';
        break;
      case 'formatExclusiveMaximum':
        out = 'formatExclusiveMaximum  boolean';
        break;
      case 'formatExclusiveMinimum':
        out = 'formatExclusiveMinimum  boolean';
        break;
      case 'formatMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += ' ' + (cond);
        break;
      case 'formatMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += ' ' + (cond);
        break;
      case 'if':
        out = ' schema "' + (e.params.failingKeyword) + '"';
        break;
      case 'maximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += ' ' + (cond);
        break;
      case 'maxItems':
        out = '';
        var n = e.params.limit;
        out += ' ' + (n);
        break;
      case 'maxLength':
        out = '';
        var n = e.params.limit;
        out += ' ' + (n) + ' ';
        break;
      case 'maxProperties':
        out = '';
        var n = e.params.limit;
        out += ' property  ' + (n);
        break;
      case 'minimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += ' ' + (cond);
        break;
      case 'minItems':
        out = '';
        var n = e.params.limit;
        out += ' ' + (n);
        break;
      case 'minLength':
        out = '';
        var n = e.params.limit;
        out += ' ' + (n) + ' ';
        break;
      case 'minProperties':
        out = '';
        var n = e.params.limit;
        out += ' property  ' + (n);
        break;
      case 'multipleOf':
        out = ' ' + (e.params.multipleOf) + ' ';
        break;
      case 'not':
        out = ' schema  "not"';
        break;
      case 'oneOf':
        out = ' schema  "oneOf" ';
        break;
      case 'pattern':
        out = ' "' + (e.params.pattern) + '"';
        break;
      case 'patternRequired':
        out = ' property  pattern "' + (e.params.missingPattern) + '"';
        break;
      case 'propertyNames':
        out = 'property \'' + (e.params.propertyName) + '\' ';
        break;
      case 'required':
        out = ' property ' + (e.params.missingProperty) + ' ';
        break;
      case 'switch':
        out = ' "switch",  ' + (e.params.caseIndex);
        break;
      case 'type':
        out = ' ' + (e.params.type);
        break;
      case 'uniqueItems':
        out = ' ( ' + (e.params.j) + '  ' + (e.params.i) + ' )';
        break;
      default:
        continue;
    }
    e.message = out;
  }
};
