'use strict';
module.exports = function localize_pl(errors) {
  if (!(errors && errors.length)) return;
  for (var i = 0; i < errors.length; i++) {
    var e = errors[i];
    var out;
    switch (e.keyword) {
      case '$ref':
        out = 'nie mona znale schematu ' + (e.params.ref);
        break;
      case 'additionalItems':
        out = '';
        var n = e.params.limit;
        out += 'nie powinien mie wicej ni ' + (n) + ' element';
        if (n == 1) {
          out += 'u';
        } else {
          out += 'w';
        }
        break;
      case 'additionalProperties':
        out = 'nie powinien zawiera dodatkowych pl';
        break;
      case 'anyOf':
        out = 'powinien pasowa do wzoru z sekcji "anyOf"';
        break;
      case 'const':
        out = 'powinien by rwny staej';
        break;
      case 'contains':
        out = 'should contain a valid item';
        break;
      case 'custom':
        out = 'powinien przej walidacj "' + (e.keyword) + '"';
        break;
      case 'dependencies':
        out = '';
        var n = e.params.depsCount;
        out += 'powinien zawiera pol';
        if (n == 1) {
          out += 'e';
        } else {
          out += 'a';
        }
        out += ' ' + (e.params.deps) + ' kiedy pole ' + (e.params.property) + ' jest obecne';
        break;
      case 'enum':
        out = 'powinien by rwny jednej z predefiniowanych wartoci';
        break;
      case 'exclusiveMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'powinien by ' + (cond);
        break;
      case 'exclusiveMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'powinien by ' + (cond);
        break;
      case 'false schema':
        out = 'boolean schema is false';
        break;
      case 'format':
        out = 'powinien zgadza si z formatem "' + (e.params.format) + '"';
        break;
      case 'formatExclusiveMaximum':
        out = 'formatExclusiveMaximum powinien by boolean';
        break;
      case 'formatExclusiveMinimum':
        out = 'formatExclusiveMinimum powinie by boolean';
        break;
      case 'formatMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'powinien by ' + (cond);
        break;
      case 'formatMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'powinien by ' + (cond);
        break;
      case 'if':
        out = 'should match "' + (e.params.failingKeyword) + '" schema';
        break;
      case 'maximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'powinien by ' + (cond);
        break;
      case 'maxItems':
        out = '';
        var n = e.params.limit;
        out += 'nie powinien mie wicej ni ' + (n) + ' element';
        if (n == 1) {
          out += 'u';
        } else {
          out += 'w';
        }
        break;
      case 'maxLength':
        out = '';
        var n = e.params.limit;
        out += 'nie powinien by duszy ni ' + (n) + ' znak';
        if (n != 1) {
          out += 'w';
        }
        break;
      case 'maxProperties':
        out = '';
        var n = e.params.limit;
        out += 'nie powinien zawiera wicej ni ' + (n) + ' ';
        if (n == 1) {
          out += 'pole';
        } else {
          out += 'pl';
        }
        break;
      case 'minimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'powinien by ' + (cond);
        break;
      case 'minItems':
        out = '';
        var n = e.params.limit;
        out += 'nie powinien mie mniej ni ' + (n) + ' element';
        if (n == 1) {
          out += 'u';
        } else {
          out += 'w';
        }
        break;
      case 'minLength':
        out = '';
        var n = e.params.limit;
        out += 'nie powinien by krtszy ni ' + (n) + ' znak';
        if (n != 1) {
          out += 'w';
        }
        break;
      case 'minProperties':
        out = '';
        var n = e.params.limit;
        out += 'nie powinien zawiera mniej ni ' + (n) + ' ';
        if (n == 1) {
          out += 'pole';
        } else {
          out += 'pl';
        }
        break;
      case 'multipleOf':
        out = 'powinien by wielokrotnoci ' + (e.params.multipleOf);
        break;
      case 'not':
        out = 'nie powinien pasowa do wzoru z sekcji "not"';
        break;
      case 'oneOf':
        out = 'powinien pasowa do jednego wzoru z sekcji "oneOf"';
        break;
      case 'pattern':
        out = 'powinien zgadza si ze wzorem "' + (e.params.pattern) + '"';
        break;
      case 'patternRequired':
        out = 'powinien mie pole pasujce do wzorca "' + (e.params.missingPattern) + '"';
        break;
      case 'propertyNames':
        out = 'property name \'' + (e.params.propertyName) + '\' is invalid';
        break;
      case 'required':
        out = 'powinien zawiera wymagane pole ' + (e.params.missingProperty);
        break;
      case 'switch':
        out = 'powinien przej walidacje pola "switch", przypadek ' + (e.params.caseIndex) + ' zawid';
        break;
      case 'type':
        out = 'powinien by ' + (e.params.type);
        break;
      case 'uniqueItems':
        out = 'nie powinien zawiera elementw ktre si powtarzaj (elementy ' + (e.params.j) + ' i ' + (e.params.i) + ' s identyczne)';
        break;
      default:
        continue;
    }
    e.message = out;
  }
};
