'use strict';
module.exports = function localize_cz(errors) {
  if (!(errors && errors.length)) return;
  for (var i = 0; i < errors.length; i++) {
    var e = errors[i];
    var out;
    switch (e.keyword) {
      case '$ref':
        out = 'reference ' + (e.params.ref) + ' nenalezena';
        break;
      case 'additionalItems':
        out = '';
        var n = e.params.limit;
        out += 'neme mt vc, ne ' + (n) + ' prv';
        if (n == 1) {
          out += 'ek';
        } else {
          out += 'k';
        }
        break;
      case 'additionalProperties':
        out = 'neme mt dal poloky';
        break;
      case 'anyOf':
        out = 'mus vyhovt alespo jednomu schmatu v "anyOf"';
        break;
      case 'const':
        out = 'mus bt konstantn';
        break;
      case 'contains':
        out = 'mus obsahovat prvek odpovdajc schmatu';
        break;
      case 'custom':
        out = 'mus vyhovt "' + (e.keyword) + '" validaci';
        break;
      case 'dependencies':
        out = '';
        var n = e.params.depsCount;
        out += ' mus mt polo';
        if (n >= 2 && n <= 4) {
          out += 'ky';
        } else if (n != 1) {
          out += 'ek';
        } else {
          out += 'ka';
        }
        out += ': ' + (e.params.deps) + ', pokud obsahuje ' + (e.params.property);
        break;
      case 'enum':
        out = 'mus bt rovna jedn hodnot z vtu';
        break;
      case 'exclusiveMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus bt ' + (cond);
        break;
      case 'exclusiveMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus bt ' + (cond);
        break;
      case 'false schema':
        out = 'schma je false';
        break;
      case 'format':
        out = 'mus bt ve formtu "' + (e.params.format) + '"';
        break;
      case 'formatExclusiveMaximum':
        out = 'formatExclusiveMaximum mus bt boolean';
        break;
      case 'formatExclusiveMinimum':
        out = 'formatExclusiveMinimum mus bt boolean';
        break;
      case 'formatMaximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus bt ' + (cond);
        break;
      case 'formatMinimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus bt ' + (cond);
        break;
      case 'if':
        out = 'should match "' + (e.params.failingKeyword) + '" schema';
        break;
      case 'maximum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus bt ' + (cond);
        break;
      case 'maxItems':
        out = '';
        var n = e.params.limit;
        out += 'nesm obsahovat vc ne ' + (n) + ' prv';
        if (n == 1) {
          out += 'ek';
        } else {
          out += 'k';
        }
        break;
      case 'maxLength':
        out = '';
        var n = e.params.limit;
        out += 'nesm bt del ne ' + (n) + ' znak';
        if (n != 1) {
          out += '';
        }
        break;
      case 'maxProperties':
        out = '';
        var n = e.params.limit;
        out += 'nesm mt vc ne ' + (n) + ' polo';
        if (n >= 2 && n <= 4) {
          out += 'ky';
        } else if (n != 1) {
          out += 'ek';
        } else {
          out += 'ka';
        }
        break;
      case 'minimum':
        out = '';
        var cond = e.params.comparison + " " + e.params.limit;
        out += 'mus bt ' + (cond);
        break;
      case 'minItems':
        out = '';
        var n = e.params.limit;
        out += 'nesm obsahovat mn ne ' + (n) + ' prv';
        if (n == 1) {
          out += 'ek';
        } else {
          out += 'k';
        }
        break;
      case 'minLength':
        out = '';
        var n = e.params.limit;
        out += 'nesm bt krat ne ' + (n) + ' znak';
        if (n != 1) {
          out += '';
        }
        break;
      case 'minProperties':
        out = '';
        var n = e.params.limit;
        out += 'nesm mt mn ne ' + (n) + ' polo';
        if (n >= 2 && n <= 4) {
          out += 'ky';
        } else if (n != 1) {
          out += 'ek';
        } else {
          out += 'ka';
        }
        break;
      case 'multipleOf':
        out = 'mus bt nsobkem ' + (e.params.multipleOf);
        break;
      case 'not':
        out = 'nesm vyhovt schmatu v "not"';
        break;
      case 'oneOf':
        out = 'mus vyhovt prv jednomu schmatu v "oneOf"';
        break;
      case 'pattern':
        out = 'mus vyhovt regulrnmu vrazu "' + (e.params.pattern) + '"';
        break;
      case 'patternRequired':
        out = 'mus obsahovat poloku vyhovujc regulrnmu vrazu "' + (e.params.missingPattern) + '"';
        break;
      case 'propertyNames':
        out = 'nzev poloky \'' + (e.params.propertyName) + '\' neodpovd schmatu';
        break;
      case 'required':
        out = 'mus obsahovat poadovanou poloku ' + (e.params.missingProperty);
        break;
      case 'switch':
        out = 'mus projt validc "switch", ppad ' + (e.params.caseIndex) + ' je nespn';
        break;
      case 'type':
        out = 'mus bt ' + (e.params.type);
        break;
      case 'uniqueItems':
        out = 'nesm obsahovat duplicitn prvky (prvky ## ' + (e.params.j) + ' a ' + (e.params.i) + ' jsou stejn)';
        break;
      default:
        continue;
    }
    e.message = out;
  }
};
